/* TheBlack PJ : 코어 CSS - core.css */

/* 공통 셋팅을 위한 클래스 */

/* 웹폰트 CDN */
@import url("https://fonts.googleapis.com/css2?family=Bagel+Fat+One&family=Black+Han+Sans&family=Dongle:wght@700&family=Gamja+Flower&family=Gugi&family=Jua&family=Nanum+Gothic&family=Noto+Sans+KR:wght@500&family=Single+Day&display=swap");

@font-face {
    font-family: 'yg-jalnan';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_four@1.2/JalnanOTF00.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'twayair';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_tway@1.0/twayair.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

/* 나눔고딕체 */
@font-face {
    font-family: NG;
    src: url(../static/fonts/NanumGothic.woff) format("woff");
}
/* Ubuntu체 */
@font-face {
    font-family: UB;
    src: url(../static/fonts/UbuntuCondensed-Regular.woff) format("woff");
}

/* 전체 글자크기 적용 */
html{
    font-size: 10px;
}

/* 전체 공통 글자체적용 */
body{
    font-family: 'Jua';
}

/* 공통 레이아웃 클래스 */
/* 속박스 */
.inbox {
    max-width: 1280px;
    margin: 0 auto;
}
/* 클리어 설정 가상요소
        float에 의한 망가짐 방지! */
.inbox::after,
.group-part::after {
    content: "";
    display: block;
    clear: both;
}
/* 파티션박스 */
.partbox {
    /* 옆으로 흐르게 */
    float: left;
    /* 보더/패딩 포함박스 */
    box-sizing: border-box;
    /* 내부패딩 */
    padding: 5px;
    /* 임시 보이게 보더 */
    /* border: 2px dotted blue; */
    /* 높이값이 속박스에 있을 경우 
            채우기셋팅 - 높이값이 파티션박스에 있으면 안씀! */
    height: 100%;
}

/* 참고: https://www.w3schools.com/css/css_rwd_grid.asp */

/* 개별영역 박스 -> 확장형 박스
        -> 개별영역 중앙속박스에 높이값을 주면
        파트박스가 늘어날 경우 높이값에 제한을 받게됨!
        확장형은 반드시 파트박스에 높이값을 설정한다! */
        
/* 클리어 가상요소 클래스 */
.clb::after{
    content: '';
    display: block;
    clear: both;
}

/* IR(Images Replacement) 
- 배경이미지 설명 숨기기 클래스 */
.ir{
    display: block;
    width: 0;
    height: 0;
    overflow: hidden;
}